<template>
  <div class="pdf-container">
    <div class="pdf-operate-btn">
      <!-- 下载 start -->
      <el-button type="primary" @click="onDownloadPDF" icon="el-icon-download" :loading="downloadLoading">下载</el-button>
      <!-- 下载 end -->

      <!-- 核实 start -->
      <el-tag v-if="verifyState == '1'">已核实</el-tag>
      <el-button type="primary" @click="onVerify" icon="el-icon-circle-check
" v-if="verifyState == '2'">核实</el-button>
      <!-- 核实 end -->
    </div>

    <!-- <iframe :src="pdfUrl" width="700" height="500" frameborder="1"></iframe> -->
    <a :href="pdfUrl" :download="pdfName" id="a_pdf"></a>
    <div ref="tableContent">
      <!-- 顶部表格数据 start -->
      <el-table :data="pdfTopTableData" style="width: 100%" border :show-header="false" :row-style="topRowStyle">
        <el-table-column v-for="(item, key) in pdfTopTableFields" :key="key" :prop="item" :label="key">
        </el-table-column>
      </el-table>
      <!-- 顶部表格数据 start -->

      <!-- 账户表格数据 start -->
      <el-table :data="pdfAccountTableData" style="width: 100%; border-top: none;" border :show-header="false" :span-method="accountSpanMethod">
        <el-table-column v-for="(item, key) in pdfAccountTableFields" :key="key" :prop="item" :label="key">
        </el-table-column>
      </el-table>
      <!-- 账户表格数据 start -->

      <!-- 展示表格数据 start -->
      <el-table :data="pdfTableData" style="width: 100%; border-top: none;" border v-loading="loading">
        <el-table-column v-for="(item, key) in pdfTableFields" :key="key" :prop="item" :label="key" align="center">
        </el-table-column>
      </el-table>
      <!-- 展示表格数据 start -->

      <!-- 前两列合并 start -->
      <el-table :data="pdfMergeTableData" style="width: 100%; border-top: none;" :span-method="arraySpanMethod" border :show-header="false" :row-style="rowStyle">
        <el-table-column v-for="(item, key) in pdfTableFields" :key="key" :prop="item" :label="key" align="center">
        </el-table-column>
      </el-table>
      <!-- 前两列合并 end -->

      <!-- 整行合并 start -->
      <el-table :data="pdfRowMergeTableData" style="width: 100%; border-top: none;" border :show-header="false" :row-style="rowStyle">
        <el-table-column v-for="(item, key) in pdfRowTableFields" :key="key" :prop="item" :label="key" align="center">
        </el-table-column>
      </el-table>
      <!-- 整行合并 end -->

      <!-- 左右两侧 start -->
      <el-table :data="pdfTwoFlankTableData" style="width: 100%; border-top: none;" border :show-header="false">
        <el-table-column v-for="(item, key) in pdfTwoFlankTableFields" :key="key" :prop="item" :label="key">
          <template slot-scope="scope">
            <div>
              <template v-if="item == 'dataMatch'">
                <div class="data-text">{{scope.row.dataMatch}}</div>
                <div class="content-text"></div>
                <div class="company-sign">
                  <div>（公司签章）</div>
                  <div class="manager">经办人：</div>
                  <div>年　　月　　日</div>
                </div>
              </template>
              <template v-else-if="item == 'dataNoMatch'">
                <div class="data-text">{{scope.row.dataNoMatch}}</div>
                <div class="content-text"></div>
                <div class="company-sign">
                  <div>（公司签章）</div>
                  <div class="manager">经办人：</div>
                  <div>年　　月　　日</div>
                </div>
              </template>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 左右两侧 end -->

      <!-- 整行 start -->
      <el-table :data="pdfExplainTableData" style="width: 100%; border-top: none;" border :show-header="false">
        <el-table-column v-for="(item, key) in pdfExplainTableFields" :key="key" :prop="item" :label="key">
          <template slot-scope="scope">
            <div class="explain">
              <div style="width: 100%;">{{ scope.row.explain }}</div>
              <div class="explain-footer">
                <div>{{ footerData.companyName }}</div>
                <div>{{ footerData.date }}</div>
              </div>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 整行 end -->
    </div>

  </div>
</template>

<script>
import { formatDate } from "../utils/utils"
export default {
  name: 'PdfStreamAccount',
  props: {
    propData: {
      type: Object,
      required: true
    },
    searchValidateForm: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      loading: false,
      downloadLoading: false, // 导出pdf按钮的加载动画
      pdfUrl: "",
      pdfName: "",

      pdfTopTableFields: {
        "表格内容": "content"
      },
      pdfTopTableData: [{
        content: "平遥古城景区管理有限公司："
      }, {
        content: "为了保证会计数据的准确性、及时性，依据后台管理系统订单数据，现将对账期间内与贵公司尚未结算的月度数据列示，如与贵公司核算数据相符，请在本函下方“数据相符”处盖章，如有不符请在“数据不符”处列明不符内容并签章。"
      }], // 顶部表格数据

      pdfAccountTableFields: {
        "字段名称": "fieldName",
        "数据名称": "dataName",
        "字段1": "field1",
        "字段2": "field2",
        "字段3": "field3",
        "字段4": "field4",
      },
      pdfAccountTableData: [{
        fieldName: "收款账户",
        dataName: "平遥古城景区管理有限公司",
      }, {
        fieldName: "收款账号",
        dataName: "0508 0144 0920 0102 334",
      }, {
        fieldName: "开 户 行",
        dataName: "中国工商银行股份有限公司平遥支行",
      }], // 账户表格数据

      pdfTableFields: { //导出Excel表格的表头设置
        "系统中景区名称": "scenicName",
        "月份": "month",
        "流水": "stream",
        "手续费0.6%": "tencnetFee",
        "可分润金额(扣0.6%)": "canFenRunAmount",
        "分润金额(扣0.6%)": "fenRunAmount"
      },
      pdfTableData: [{
        scenicName: '平遥古城',
        month: '2020年5月',
        stream: '103356.00',
        tencnetFee: '620.14',
        canFenRunAmount: '102735.86',
        fenRunAmount: '20547.17'
      }], // pdf流水对账单表格数据

      pdfMergeTableData: [{
        scenicName: '分润比例：',
        month: '',
        stream: '',
        tencnetFee: '',
        canFenRunAmount: '',
        fenRunAmount: '20%'
      }], // pdf合并表格数据

      pdfRowTableFields: {
        "公式": "equation"
      },
      pdfRowMergeTableData: [{
        equation: '付款金额=实际结算金额（不含税）+收到的增值税专用发票税额或=分润金额+代交税率x%的增值税税额'
      }], // pdf合并表格数据

      pdfTwoFlankTableFields: {
        "数据相符": "dataMatch",
        "数据不符": "dataNoMatch"
      },
      pdfTwoFlankTableData: [{
        dataMatch: "1、数据相符，核对无误。",
        dataNoMatch: "2、数据不符，以下为不符内容："
      }], // pdf两列表格数据（数据核对部分）

      pdfExplainTableFields: {
        "说明": "explain"
      },
      pdfExplainTableData: [{
        explain: '请贵公司尽快核对，并将已签章对账单反馈给我公司负责对账人员。我公司已代扣代缴6%税率的增值税款，贵公司需提供等税率的增值税专用发票，我公司将在收到核对相符的对账单及增值税专用发票后，安排支付增值税发票上的价税合计金额。'
      }], // pdf底部说明部分表格数据

      footerData: {
        companyName: "平遥九星科技有限公司",
        date: "2021年9月3日"
      },

      verifyState: "2"
    }
  },
  created() {
    // this.getPdfTableData()
    this.getVerifyState()
    // this.pdfTopTableData[0].content = this.propData.合作主图
  },
  mounted() {

  },
  methods: {
    // 获取核实状态
    getVerifyState() {
      // 状态模拟
      var verifyState = "2" // 1为已核实，2为未核实
      if (verifyState == "1") {
        console.log("已核实");
        this.verifyState = "1"
      } else if (verifyState == "2") {
        console.log("未核实");
        this.verifyState = "2"
      }
    },
    // 下载pdf
    onDownloadPDF() {
      // 后端导出pdf方式
      var companyId = this.searchValidateForm.companyId
      if (companyId == "" || !companyId) {
        companyId = null
      }

      var spotId = this.searchValidateForm.spotId
      if (spotId == "" || !spotId) {
        spotId = null
      }

      var startEndDate = this.searchValidateForm.startEndDate
      var beginDate, endDate
      if (startEndDate == "" || !startEndDate) {
        beginDate = formatMonth()
        endDate = formatMonth()
      } else {
        beginDate = this.searchValidateForm.startEndDate[0]
        endDate = this.searchValidateForm.startEndDate[1]
      }


      var paymentType = this.searchValidateForm.paymentType
      if (paymentType == "" || !paymentType) {
        paymentType = null
      } else {
        paymentType.map((type, index) => {
          if (type == "1") {
            paymentType[index] = "WECHAT"
          }
          if (type == "2") {
            paymentType[index] = "SAVING"
          }
          if (type == "3") {
            paymentType[index] = "SAVING_WECHAT"
          }
          if (type == "4") {
            paymentType[index] = "DEPOSIT"
          }
        })

        paymentType = paymentType.join(",")
        // console.log(paymentType);
      }

      this.downloadLoading = true
      this.pdfUrl = `/system/account_statement/download?company=${companyId}&spot=${spotId}&beginDate=${beginDate}&endDate=${endDate}&paymentType=${paymentType}`
      this.pdfName = "流水对账单"
      // 注意这里需要等待dom加载完成后才能进行下载
      this.$nextTick(() => {
        document.getElementById("a_pdf").click()
        this.downloadLoading = false
      })

      /* 前端导出pdf方式
      this.$confirm('确认导出PDF文件?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.downloadLoading = true
        var promiseObj = this.$PDFOutput(this.$refs["tableContent"])
        promiseObj.then((value) => {
          this.pdfUrl = value
          this.downloadLoading = false

          this.pdfName = "流水对账单"
          document.getElementById("a_pdf").click()

          this.$message({
            type: 'success',
            message: '下载成功'
          });
        })

      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消导出PDF文件'
        });
      })
      */
    },
    // 核实
    onVerify() {
      console.log("点击了核实按钮");
    },
    getPdfTableData() {
      // 模拟请求
      this.loading = true
      setTimeout(() => {
        let pdfTableData = [{
          scenicName: '平遥古城',
          month: '2020年5月',
          stream: '103356.00',
          tencnetFee: '620.14',
          canFenRunAmount: '102735.86',
          fenRunAmount: '20547.17'
        }, {
          scenicName: '平遥古城',
          month: '2020年5月',
          stream: '103356.00',
          tencnetFee: '620.14',
          canFenRunAmount: '102735.86',
          fenRunAmount: '20547.17'
        }, {
          scenicName: '平遥古城',
          month: '2020年5月',
          stream: '103356.00',
          tencnetFee: '620.14',
          canFenRunAmount: '102735.86',
          fenRunAmount: '20547.17'
        }]
        this.pdfTableData = pdfTableData
        this.loading = false
      }, 1000)
    },
    topRowStyle({ row, rowIndex }) {
      if (rowIndex == 0) {
        return {
          "font-weight": "800"
        }
      }
    },
    accountSpanMethod({ row, column, rowIndex, columnIndex }) {
      // 合并第一列
      if (columnIndex === 1) {
        return [1, 5];
      } else if (columnIndex === 2 || columnIndex === 3 || columnIndex === 4 || columnIndex === 5) {

      }
    },
    // pdf流水对账单表格合并
    arraySpanMethod({ row, column, rowIndex, columnIndex }) {
      // 合并第0列
      if (columnIndex === 0) {
        return [1, 2];
      } else if (columnIndex === 1) {
        // 将第一列设为0
        return [0, 0];
      }

      // 合并2行2列
      if (rowIndex === 2 && columnIndex === 2) {
        return [1, 3]
      } else if (rowIndex === 2) {
        if (columnIndex === 3 || columnIndex === 4) {
          return [0, 0]
        }
      }
    },

    // pdfMergeTableData表格的样式设置
    rowStyle({ row, rowIndex }) {
      return {
        "font-weight": "800"
      }
    }
  },
  watch: {
    propData(val, oldVal) {
      if (val !== oldVal) {
        // console.log(this.propData);
        // 如果propData为空则直接返回
        if (!this.propData) return

        this.pdfTopTableData[0].content = this.propData.收款账户 + "："

        this.pdfAccountTableData[0].dataName = this.propData.收款账户
        this.pdfAccountTableData[1].dataName = this.propData.收款账号
        this.pdfAccountTableData[2].dataName = this.propData.开户行

        this.pdfTableData = [{
          scenicName: this.propData.景区名称,
          month: this.propData.月份,
          stream: this.propData.流水,
          tencnetFee: this.propData.手续费,
          canFenRunAmount: this.propData.可分润金额,
          fenRunAmount: this.propData.分润金额
        }]

        this.pdfMergeTableData[0].fenRunAmount = this.propData.分润比例

        this.footerData.companyName = this.propData.合作主体

        this.footerData.date = formatDate()
      }
    },
    searchValidateForm(val, oldVal) {
      if (val !== oldVal) {
        if (!this.searchValidateForm) return
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.pdf-operate-btn {
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.data-text {
  line-height: 30px;
  font-weight: 800;
}
.content-text {
  line-height: 30px;
  margin-top: 30px;
  margin-bottom: 30px;
}
.company-sign {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  margin-right: 100px;
  line-height: 30px;
}

.company-sign .manager {
  width: 150px;
}

.explain {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
}

.explain-footer {
  line-height: 30px;
  margin-top: 30px;
  margin-right: 100px;
}
</style>